.ms-simulation {
  position: relative;
  margin: 300px 20px 20px 100px;
  width: 600px;
  height: 300px;
  background-color: #eeeeee;
}

.point {
  position: absolute;
  border-radius: 100%;
  width: 6px;
  height: 6px;
  margin: -3px 0 0 -3px;
}

.blendeOben {
  position: absolute;
  top: 0;
  right: 0;
  background-color: black;
  width: 3px;
  height: 145px;
}

.blendeUnten {
  position: absolute;
  bottom: 0;
  right: 0;
  background-color: black;
  width: 3px;
  height: 145px;
}

.kondensator {
  position: absolute;
  height: 10px;
  width: 99%;
  border: 1px solid;
}

.kondensatorOben {
  top: -12px;
}

.kondensatorUnten {
  bottom: -12px;
}

.ionenQuelle {
  width: 40px;
  height: 30px;
  position: absolute;
  top: 135px;
  left: -80px;
  background: gray;
}

.blendeAnalysatorUnten {
  right: -50px;
}

.schirm {
  right: -50px;
  position: absolute;
  top: -265px;

  background-color: brown;
  width: 3px;
  height: 410px;
}

.skala {
  position: absolute;
  right: -50px;
  top: -260px;
}

.magnetfeldZeichen {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
}

.magnetfeldZeichen div {
  background-image: url('./Magnetfeld.svg');
  background-repeat: no-repeat;
  background-position: center;
  width: 10px;
  height: 10px;
}

.efeldZeichen {
  position: absolute;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

.efeldZeichen div,
.efeldZeichen img {
  height: 100%;
  display: block;
  float: left;
  width: 11px;
}

.analysator {
  position: absolute;
  background-color: #eeeeee;
  width: 270px;
  height: 560px;
  right: -320px;
  top: -260px;
  z-index: -1;
}

.controls {
  padding-left: 100px;
  padding-top: 10px;
  position: absolute;
  top: 20px;
}
.controls input {
  font-size: 150%;
  margin: 20px 10px;
  padding: 15px;
}

.controls select {
  font-size: 150%;
  margin: 5px 10px;
  padding: 15px;
}

.settingFilter {
  position: absolute;
  top: 650px;
  left: 300px;
}

.settingAnalysator {
  position: absolute;
  top: 650px;
  left: 760px;
}

.setting input {
  margin: 10px 10px;
}

.anweisungen {
  margin-top: 150px;
  padding: 20px; 
  font-size: 130%;
}